  --white-1: rgba(242,242,242, 1);
  --gray-1: rgb(226, 222, 222); 
  --gray-2:  rgba(191,191,191, 1); 
  --gray-3: rgba(88,89,89, 1); 
  --black-1: rgba(13,13,13, 1); 
}

